<template>
    <div class="classfix">
        <van-nav-bar
        title="商品分类"
        right-text="搜索"
        @click-right="goserch"
        />
        <div class="classfix-content">
            <div class="aside fl">
                <div class="aside-item" @click="getcat(item,index)" :class="{isActive : item.cat_deleted}" v-for="(item,index) in classfox" :key="index">
                    {{item.cat_name}}
                </div>
            </div>
            <div class="content fr">
                <div class="cle clearfix" v-for="(ve,its) in ggg" :key="its">
                    <div class="txt fl">{{ve.cat_name}}</div>
                    <div class="cost fl" v-for="(gov,it) in ve.children" :key="it" @click="goDetail(gov.cat_id)">
                        <div class="cost-img">
                            <img :src="gov.cat_icon" alt="">
                        </div>
                        <div class="cost-txt">
                            {{gov.cat_name}}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import "../../assets/css/supermarket.css"
export default {
    data(){
        return{
            classfox:'',
            coo: 0,
            visa:'',
            ggg:''
        }
    },
    created(){
        this.getcategor();
        let item0 = {
            cat_id:1,
            cat_name:"大家电"
        }
        this.getProduc(item0)
    },
    methods: {
        goserch(){
            this.$router.push('/serch')
        },
        goDetail(cat_id) {
            this.$router.push({ name: "Details", query: { cat_id } });
        },

        // 商品分类
        getcategor(){
            this.axios({
                method: 'GET',
                url:'/categories',
                data:{}
            }).then(result => {
                
                if(result.status == 200){
                    result.data.message[0].cat_deleted = true
                    this.classfox = result.data.message;
                }
            })
        },
        getcat(item,index){

            for(let i=0;i<this.classfox.length;i++){
                if(this.classfox[i].cat_deleted){
                    this.classfox[i].cat_deleted = false;
                    break;
                }
            }
            item.cat_deleted = true
            this.getProduc(item)
        },
        //获取商品类型
        getProduc(item){
            let data = {
                key: item.cat_name,
                value: item.cat_id
            }
           
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0
            });

            this.axios({
                method: 'GET',
                url: '/categories',
                params:{}
            }).then(result => {
                if(result.status == 200){
                    this.$toast.clear();
                    this.visa = result.data.message.filter( v => v.cat_id== item.cat_id );
                    this.ggg = this.visa[0].children;
                }
            }).catch(err => {
                this.$toast.clear();
            })
        },
    }
}
</script>